/* иконки */
.iconPrimary {
  transition: color 0.3s ease-in-out;

  color: var(--icon_primary_normal);

  &:hover {
    color: var(--icon_primary_hover);
  }

  &_active {
    color: var(--icon_primary_active);
  }

  &:disabled {
    color: var(--icon_primary_disable);
  }
}

.iconOpacity {
  transition: color 0.3s ease-in-out;

  color: var(--icon_primary_normal);
  &:hover {
    opacity: 0.6;
  }
  &_active {
    color: var(--icon_primary_active);
  }

  &:disabled {
    color: var(--icon_primary_disable);
  }
}

