
.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-vertical-middle {
  vertical-align: middle;
}

.text-pre-wrap {
  white-space: pre-wrap;
}

.text-no-wrap {
  white-space: nowrap;
}

.text-uppercase {
  text-transform: uppercase;
}

.text-success {
  color: var(--service-success);
}

.text-info {
  color: var(--txt_primary);
}

.text-warning {
  color: var(--service-warning);
}

.text-error {
  color: var(--service-error);
}
