.headerPrize {
    display: flex;
    flex-direction: column;
    align-items: center;
    &__coins {
      display: flex;
      margin-bottom: .5rem;
    }
    &__description{
      font-size: 1.6rem;
      line-height: 2rem;
    }
  }

.coin {
    position: relative;
    display: flex;
    width: max-content;
    margin-right: -.7rem;
    &:last-child {
      margin-right: 0;
    }
    &__amount {
      position: absolute;
      z-index: 3;
      left: 50%;
      top: 50%;
      font-size: 1.4rem;
      transform: translate(-50%, -50%);
    }
  }