@import 'src/styles/mixins';

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  height: calc(47vh);
  padding: 2rem;
  background-color: var(--bg-widget);
}

.tableWrapper {
  padding-right: 1rem;
  overflow-y: auto;
  overflow-x: hidden;
  @include scrollBar;
}

.table {
  @include defaultStyleTable;
  @include scrollBar;
}

.cellCustom {
  padding-inline: 14px 10px;
  background: var(--bg);
  border-color: var(--stroke-default);
  display: flex;
  align-items: center;
}

.customHeaderCell {
  background: var(--table_header);
  font-weight: 400;
  font-size: 16px;
  border-color: var(--stroke-default);
  text-align: left;
  //line-height: 136%;
  color: var(--on_primary);
  padding-inline: 14px 10px;
}

.cell {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &__box {
    display: flex;
    align-items: center;
    gap: 0.3rem;
  }
  &__different {
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.4rem;
    width: 4.2rem;
    height: 3rem;
    border-radius: 0.6rem;
    background: rgba(var(--service-sussess_rgb), 0.05);
    color: var(--service-sussess);
  }
  &__negative {
    color: var(--service-error);
    background: rgba(var(--service-error_rgb), 0.05);
  }
  &__description {
    font-size: 1.4rem;
    color: var(--service-sussess);
  }
}

.userCell {
  width: 100%;
  display: flex;
  align-items: center;
  gap: 2rem;
  &__avatar {
    width: 5rem;
    height: 5rem;
    border-radius: 50%;
  }
  &__fullname {
    font-size: 2rem;
  }
}
