body {
  overflow-x: hidden;

  font-family: 'Roboto', sans-serif;
  font-size: 14px;
  font-weight: 400;
  font-style: normal;
  line-height: 1.5;
}

html,
body,
#root {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  min-height: 100%;
}

.disabledElement {
  pointer-events: none;
}
