[theme='dark_color'] {
  --bg: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6)), var(--colorizer);
  --bg_rgb: rgba(var(--colorizer_rgb), 0.4);
  --app-color: #fff;
  --bg-w-head: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2)), var(--primary);
  --bg-widget: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --bg_settings_menu: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), linear-gradient(270deg, var(--colorizer) 50%, rgba(var(--colorizer_rgb), 0.86) 100%);
  /* для aside*/
  --bg-menu_01: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --bg-menu_01_open: linear-gradient(90deg, rgba(0, 0, 0, 0.47) 21.17%, rgba(0, 0, 0, 0.47) 100%), linear-gradient(90deg, var(--colorizer) 20.83%, rgba(var(--colorizer_rgb), 0.80) 100%);
  --menu_01_active: var(--on_primary);
  /* UserPanel*/
  --bg-menu_02: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);

  --surface_01: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);
  --surface_02: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --surface_02_rgb: rgba(var(--colorizer_rgb), 0.4);
  --surface_03: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), var(--colorizer);
  --surface_04: rgba(0, 0, 0, 0.3);
  --surface_05: rgba(100, 110, 131, .9);
  --surface_06: linear-gradient(0deg, rgba(var(--colorizer_rgb), 0.4), rgba(var(--colorizer_rgb), 0.4)), rgba(53, 61, 76, 1);
  --speedo-arrow: #737373;
  /* для всех темных тем */
  --stroke-default: rgba(255, 255, 255, 0.2);
  --stroke-active: rgba(255, 255, 255, 0.6);
  --stroke-separator: rgba(255, 255, 255, 0.4);
  --secondary-normal: rgba(255, 255, 255, 0.2);
  --secondary-onSecondary: #fff;
  --secondary-hover: rgba(255, 255, 255, 0.3);
  --secondary-active: rgba(255, 255, 255, 0.1);
  /* для шрифтов */
  --txt_1: #fff;
  --txt_2: rgba(255, 255, 255, 0.6);
  --txt_3: rgba(255, 255, 255, 0.4);
  --txt_4: rgba(255, 255, 255, 0.3);
  --txt_5: rgba(255, 255, 255, 0.8);
  --txt_link: #44A5FF;
  --txt_link_hover: #ffffff;
  --txt_widget: #fff;
  --txt_tertiary: #f16B52;
  --txt_primary: #0A6CF5;
  /* для маленьких виджетов*/
  --bg-miniwidget: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), #00B5EA;
  --miniwidget-stroke: #00b5ea;
  --bg-mini: linear-gradient(0deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.2)), var(--colorizer);
   /* для иконок*/
   --icon_primary_normal: var(--primary);
   --icon_primary_hover: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), var(--primary);
   --icon_primary_active: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)), var(--primary);
   --icon_primary_disable: var(--primary);

   --icon_secondary_normal: var(--secondary-normal);
   --icon_secondary_hover: var(--secondary-hover);
   --icon_secondary_active: var(--secondary-active);
   --icon_secondary_disable: var(--secondary-normal);

   --icon_tertiary_normal: var(--tertiary);
   --icon_tertiary_hover: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)), var(--tertiary);
   --icon_tertiary_active: linear-gradient(0deg, rgba(0, 0, 0, 0.1), rgba(0, 0, 0, 0.1)), var(--tertiary);
   --icon_tertiary_disable: var(--tertiary);

   --icon_on_primary: #ffffff;
   --icon_on_secondary: #ffffff;
   --icon_on_tertiary: #ffffff;

   --icon_on_menu_normal: rgba(255, 255, 255, 0.6);
   --icon_on_menu_hover: #ffffff;
   --icon_on_menu_active: #ffffff;
   --icon_on_menu_disable: #ffffff;

   --icon_on_widget_header_normal: #ffffff;
   --icon_on_widget_header_hover: linear-gradient(0deg, rgba(255, 255, 255, 0.6), rgba(255, 255, 255, 0.6)), var(--primary);
   --icon_on_widget_header_active: #ffffff;
   --icon_on_widget_header_disable: rgba(#ffffff, 0.4);

   --icon_default_normal: rgba(255, 255, 255, 0.2);
   --icon_default_hover: rgba(255, 255, 255, 0.2);
   --icon_default_active: rgba(255, 255, 255, 0.2);
   --icon_default_disable: rgba(255, 255, 255, 0.4);

   /* для кнопки выбора арма */
  --border_color: rgba(255, 255, 255, 0.2);

  /* для таблиц */
  --table_header:  linear-gradient(0deg, rgba(65, 98, 156, 0.4), rgba(65, 98, 156, 0.4)), var(--colorizer);
  --table_default: linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_hover: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_select: linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_error_default: linear-gradient(0deg, rgba(250, 75, 75, 0.1), rgba(250, 75, 75, 0.1)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_error_hover: linear-gradient(0deg, rgba(250, 75, 75, 0.1), rgba(250, 75, 75, 0.1)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_error_select: linear-gradient(0deg, rgba(250, 75, 75, 0.1), rgba(250, 75, 75, 0.1)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_warning_default: linear-gradient(0deg, rgba(243, 213, 94, 0.5), rgba(243, 213, 94, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_warning_hover: linear-gradient(0deg, rgba(243, 213, 94, 0.5), rgba(243, 213, 94, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_warning_select: linear-gradient(0deg, rgba(243, 213, 94, 0.5), rgba(243, 213, 94, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_success_default: linear-gradient(0deg, rgba(10, 209, 163, 0.1), rgba(10, 209, 163, 0.1)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_success_hover: linear-gradient(0deg, rgba(10, 209, 163, 0.1), rgba(10, 209, 163, 0.1)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_success_select: linear-gradient(0deg, rgba(10, 209, 163, 0.1), rgba(10, 209, 163, 0.1)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_total_default: linear-gradient(0deg, rgba(0, 166, 166, 0.4), rgba(0, 166, 166, 0.4)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_total_hover: linear-gradient(0deg, rgba(0, 166, 166, 0.4), rgba(0, 166, 166, 0.4)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_total_select: linear-gradient(0deg, rgba(0, 166, 166, 0.4), rgba(0, 166, 166, 0.4)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);
   /* для теней */
  --shadow_2:drop-shadow(0px -10px 20px rgba(0, 0, 0, 0.08)) drop-shadow(0px 10px 20px rgba(0, 0, 0, 0.08)) drop-shadow(0px 10px 18px rgba(0, 0, 0, 0.12));

  /* цвета ячеек для таблиц */

  --table_cell-green_default:  linear-gradient(0deg, rgba(23, 74, 74, 0.5), rgba(23, 74, 74, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_cell-green_hover: linear-gradient(0deg, rgba(23, 74, 74, 0.5), rgba(23, 74, 74, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_cell-green_select: linear-gradient(0deg, rgba(23, 74, 74, 0.5), rgba(23, 74, 74, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_cell-lightgreen_default: linear-gradient(0deg, rgba(37, 108, 87, 0.5), rgba(37, 108, 87, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_cell-lightgreen_hover: linear-gradient(0deg, rgba(37, 108, 87, 0.5), rgba(37, 108, 87, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_cell-lightgreen_select: linear-gradient(0deg, rgba(37, 108, 87, 0.5), rgba(37, 108, 87, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_cell-blue_default: linear-gradient(0deg, rgba(26, 75, 91, 0.5), rgba(26, 75, 91, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_cell-blue_hover: linear-gradient(0deg, rgba(26, 75, 91, 0.5), rgba(26, 75, 91, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_cell-blue_select: linear-gradient(0deg, rgba(26, 75, 91, 0.5), rgba(26, 75, 91, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_cell-darkblue_default: linear-gradient(0deg, rgba(0, 166, 166, 0.4), rgba(0, 166, 166, 0.4)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_cell-darkblue_hover: linear-gradient(0deg, rgba(0, 166, 166, 0.4), rgba(0, 166, 166, 0.4)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_cell-darkblue_select: linear-gradient(0deg, rgba(0, 166, 166, 0.4), rgba(0, 166, 166, 0.4)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_cell-yellow_default: linear-gradient(0deg, rgba(182, 144, 6, 0.5), rgba(182, 144, 6, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_cell-yellow_hover: linear-gradient(0deg, rgba(182, 144, 6, 0.5), rgba(182, 144, 6, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_cell-yellow_select: linear-gradient(0deg, rgba(182, 144, 6, 0.5), rgba(182, 144, 6, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_cell-black_default: linear-gradient(0deg, rgba(9, 11, 17, 0.5), rgba(9, 11, 17, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_cell-black_hover: linear-gradient(0deg, rgba(9, 11, 17, 0.5), rgba(9, 11, 17, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_cell-black_select: linear-gradient(0deg, rgba(9, 11, 17, 0.5), rgba(9, 11, 17, 0.5)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  --table_cell-red_default: linear-gradient(0deg, rgba(250, 75, 75, 0.2), rgba(250, 75, 75, 0.2)), linear-gradient(0deg, rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), var(--colorizer);
  --table_cell-red_hover: linear-gradient(0deg, rgba(250, 75, 75, 0.2), rgba(250, 75, 75, 0.2)), linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25)), var(--colorizer);
  --table_cell-red_select: linear-gradient(0deg, rgba(250, 75, 75, 0.2), rgba(250, 75, 75, 0.2)), linear-gradient(0deg, rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.3)), var(--colorizer);

  /* цвета для алертов */

  --alert_color-info: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), rgba(var(--service-info_rgb), 0.8);
  --alert_color-warning: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), rgba(var(--service-warning_rgb), 0.8);
  --alert_color-error: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), rgba(var(--service-error_rgb), 0.8);
  --alert_color-success: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), rgba(var(--service-sussess_rgb), 0.8);
  --alert_color-purple: linear-gradient(0deg, rgba(0, 0, 0, 0.4), rgba(0, 0, 0, 0.4)), rgba(var(--service-purple_rgb), 0.8);
  --alert_color-gray: rgba(255, 255, 255, 0.1);
  --alert_color-gray_border: var(--stroke-default);

  --progress-bar-shadow: rgba(0, 0, 0, 0.5);
}
