@mixin scrollBar {
  &::-webkit-scrollbar {
    width: 4px;
    height: 8px;
  }

  &::-webkit-scrollbar-track {
    background: var(--bg);
  }

  &::-webkit-scrollbar-thumb {
    background: var(--surface_06);

    &:hover {
      background: var(--primary_hover);
    }
  }

  &::-webkit-scrollbar-track,
  &::-webkit-scrollbar-thumb {
    border-radius: 2px;
  }
}

@mixin defaultStyleTable {
  border: 1px solid var(--stroke-default);
  border-radius: 5px;
  background: transparent;
  height: auto;
  width: 100%;
  color: var(--txt_1);

  & * {
    outline: none;
    background: transparent;
  }
}
