.container {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0.4rem 2rem 1.5rem 2rem;
  background-color: var(--bg-widget);
  border-radius: 0.5rem;
  margin-bottom: 2rem;
}

.levelScale {
  display: flex;
  align-items: flex-end;
  gap: 2rem;
  width: 100%;
}

.level {
  position: relative;
  display: flex;
  flex-direction: column;
  &::after {
    content: '';
    position: absolute;
    left: -1rem;
    top: 60%;
    width: 0.1rem;
    height: 1rem;
    background-color: var(--txt_4);
  }
  &:first-child {
    &::after {
      content: none;
    }
  }
  &__progressbar {
    position: relative;
    width: 28rem;
    height: 1rem;
    background-color: #2f4162;
    position: relative;
    border-radius: 0.5rem;
    margin-bottom: 0.5rem;
  }
  &__fill {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    border-radius: 0.5rem;
    background: linear-gradient(90deg, #00fff0 30%, #0a6cf5 100%);
  }
  &__description {
    display: flex;
    align-items: flex-end;
    gap: 1rem;
    margin: 0 auto;
    margin-bottom: 0.4rem;
  }
  &__amount {
    display: flex;
    justify-content: space-between;
  }
}


.excellent{
  width: 22rem;
  color: var(--txt_4);
}