.header {
  position: relative;
  // height: 22rem;
  width: 100%;
  margin-bottom: 2rem;

  &__topBox {
    position: relative;
    width: 100%;
    height: 11rem;
    padding: 1rem 1.8rem;
    border-radius: 0.5rem;
  }
  &__title {
    position: relative;
    z-index: 1;
    font-size: 3.2rem;
    font-weight: 700;
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 0;
    border-radius: 5px;
    height: 11rem;
    width: 100%;
  }
  &__avatar {
    position: absolute;
    top: 50%;
    transform: translate(-50%, -50%);
    left: 50%;
    width: 15.5rem;
    height: 15.5rem;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    background-color: var(--bg);
  }

  &__progressBar {
    position: absolute;
    width: 14.2rem;
    height: 14.2rem;
    background-color: var(--bg-mini);
    border-radius: 50%;
    position: relative;
    overflow: hidden;
    &::before {
        content: '';
        position: absolute;
        top: 100%; /* Управляет размером внутренней пустоты. 10% оставляет внутренний радиус в 80% от внешнего радиуса. */
        left: 100%;
        width: 50%;
        height: 50%;
        background-color: var(--bg);
        border-radius: 50%;
        z-index: 3;
    }
  }
 
  &__avatarImg {
    border-radius: 50%;
  }
  &__avatartLvl {
    position: absolute;
    top: 1.9rem;
    left: 0;
    z-index: 3;
  }
  &__fill {
    width: 100%;
    height: 100%;
    background: conic-gradient(#00fff0 0%, #0a6cf5 30%, transparent 30% 100%);
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    z-index: 2;
    align-items: center;
    justify-content: center;

    &__avatarImg {
      box-sizing: content-box;
      width: 11.7rem;
      height: 11.7rem;
      border: 0.9rem solid var(--bg);
      border-radius: 50%;
      background-color: var(--bg);
    }
  }
}

.bottomBox {
  display: flex;
  align-self: center;
  justify-content: space-between;
  padding: 1.5rem 2rem;
  height: 100%;
  background-color: var(--bg-widget);
  height: 11rem;
  &__left {
    display: flex;
    align-items: center;
    gap: 2.5rem;
  }
  &__right{
    display: flex;
    align-items: center;
    gap: 2rem;
    padding: 1.85rem 2rem;
    background-color: var(--bg);
    border-radius: .5rem;
  }
  &__description{
    font-size: 1.4rem;
    & span{
      font-size: 1.4rem;
      color: var(--primary);
    }
  }
}

.trophys {
  display: flex;
  align-items: center;
  gap: 1rem;
  &__icon{
    width: 5rem;
    height: 4rem;
  }
}
