.statsField {
  position: relative;
  width: 100%;
  height: 38.2rem;
  &__title {
    font-size: 2.8rem;
    font-weight: 500;
    // line-height: 1;
  }
  &__lvl {
    font-size: 2.8rem;
    font-weight: 500;
    line-height: 1;
    color: var(--txt_link);
  }
  &__pedestal {
    position: absolute;
    top: 2.2rem;
    left: 50%;
    transform: translateX(-50%);
  }
  &__image {
    height: 36rem;
    width: 89.4rem;
  }
  &__firstUser {
    position: absolute;
    left: 50%;
    top: 50%;
    display: flex;
    flex-direction: column;
    align-items: center;
    transform: translate(-50%, -35%);
  }
   
  &__firstAvatar {
    position: relative;
    border: 0.5rem solid var(--service-warning);
    width: 12rem;
    height: 12rem;
    margin-bottom: 2rem;
    &::after {
      content: '';
      position: absolute;
      width: 4.633rem;
      height: 4.247rem;
      top: -2.4rem;
      right: -1rem;
      background-size: contain;
      background-image: url('../../../../assets/img/crown.png');
    }
  }

  &__secondUser {
    position: absolute;
    display: flex;
    flex-direction: column;
    align-items: center;
    top: 15rem;
    left: 16rem;
  }

  &__secondAvatar{
    border: 0.5rem solid #66768e;
    width: 9rem;
    height: 9rem;
    margin-bottom: 2rem;
  }

  &__thirdUser {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    top: 17rem;
    right: 18rem;
  }

  &__thirdAvatar{
    border: 0.5rem solid var(--tertiary);
    width: 9rem;
    height: 9rem;
  }
  &__avatarBig {
    width: 9.2rem;
    height: 9.2rem;
  }
  &__avatarSmall {
    width: 7rem;
    height: 7rem;
  }
  &__fullName{
    font-size: 1.6rem;
    font-weight: 500;
    margin-bottom: .7rem;
  }
}

.avatar {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
}
