@font-face {
  font-family: 'Roboto';
  font-weight: 100;
  font-style: normal;

  src: url('@/assets/fonts/Roboto-Thin.woff2') format('woff2'), url('@/assets/fonts/Roboto-Thin.woff') format('woff');
}

@font-face {
  font-family: 'Roboto';
  font-weight: 300;
  font-style: normal;

  src: url('@/assets/fonts/Roboto-Light.woff2') format('woff2'), url('@/assets/fonts/Roboto-Light.woff') format('woff');
}

@font-face {
  font-family: 'Roboto';
  font-weight: 500;
  font-style: normal;

  src: url('@/assets/fonts/Roboto-Medium.woff2') format('woff2'),
    url('@/assets/fonts/Roboto-Medium.woff') format('woff');
}

@font-face {
  font-family: 'Roboto';
  font-weight: 900;
  font-style: normal;

  src: url('@/assets/fonts/Roboto-Black.woff2') format('woff2'), url('@/assets/fonts/Roboto-Black.woff') format('woff');
}

@font-face {
  font-family: 'Roboto';
  font-weight: 700;
  font-style: normal;

  src: url('@/assets/fonts/Roboto-Bold.woff2') format('woff2'), url('@/assets/fonts/Roboto-Bold.woff') format('woff');
}

@font-face {
  font-family: 'Roboto';
  font-weight: 400;
  font-style: normal;

  src: url('../assets/fonts/Roboto-Regular.woff2') format('woff2'),
    url('../assets/fonts/Roboto-Regular.woff') format('woff');
  font-display: swap;
}

html {
  font-size: calc(100vw / 1920 * 10);
}

body {
  margin: 0;
  font-family: 'Roboto', sans-serif;
  font-weight: 700;
  /* -webkit-font-smoothing: antialiased; */
  /* -moz-osx-font-smoothing: grayscale; */
  background-color: linear-gradient(0deg, rgba(0, 0, 0, 0.6), rgba(0, 0, 0, 0.6));
}

.display-none {
  display: none !important;
  width: 0 !important;
  height: 0 !important;
}

#root {
  height: 100%;
}

.maxHeight {
  height: 100%;
}

.container {
  width: 120rem;
  margin: 0 auto;
  padding: 0 1.5rem;
  background: var(--bg-widget);
}

.swiper {
  position: initial!important;
}

.swiper-button-prev{
  left: -40px!important;
}
.swiper-button-next {
  right: -40px!important;
}
.swiper-button-next::after {
  font-size: 20px !important;
  color: var(--bg-widget) !important;
  right: -10px;
}
.swiper-button-prev::after {
  font-size: 20px !important;
  color: var(--bg-widget) !important;
  left: -10px;
}

.react-grid-item.react-grid-placeholder {
  background: rgba(255, 255, 255, 0.4) !important;
  border-radius: 5px;
}

.react-resizable-handle {
  background-image: none !important;
  width: 24px !important;
  height: 24px !important;
}