.react-calendar {
  width: 340px;
  max-width: 100%;
  border: none;
  background: transparent;
  font-family: 'Roboto', Arial sans-serif;
  font-weight: 400;
  line-height: 1.125em;
}

.react-calendar--doubleView {
  width: 700px;
}

.react-calendar--doubleView .react-calendar__viewContainer {
  display: flex;
  margin: -0.5em;
}

.react-calendar--doubleView .react-calendar__viewContainer > * {
  width: 50%;
  margin: 0.5em;
}

.react-calendar,
.react-calendar *,
.react-calendar *:before,
.react-calendar *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

.react-calendar button {
  margin: 0;
  border: 0;
  outline: none;
}

.react-calendar button:enabled:hover {
  cursor: pointer;
}

.react-calendar__navigation {
  display: flex;
  height: 60px;
  margin-bottom: 0;
  padding: 0 10px;

  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background: var(--bg-w-head);
}

.react-calendar__navigation button {
  flex-shrink: 0;
}

.react-calendar__navigation__label {
  width: 100%;
}

.react-calendar__navigation button {
  display: flex;
  min-width: 44px;
  background: none;
  align-items: center;
  justify-content: center;
  flex-shrink: 1;
}

.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: var(--bg-w-head);
}

.react-calendar__tile--now {
  background: var(--surface_01);
}

.react-calendar__tile--active.react-calendar__tile--now {
  background: var(--primary_hover);
}


.react-calendar__navigation__label {
  color: var(--txt_widget);

  font-size: 16px;

  flex-shrink: 0;
}

.react-calendar__navigation__label:hover {
  cursor: pointer;

  opacity: 0.6;
}


.react-calendar__navigation__arrow {
  color: var(--icon_on_widget_header_normal);

  font-size: 24px;
}

.react-calendar__navigation__arrow:hover,
.react-calendar__navigation__arrow:focus {
  opacity: 0.4;
}


.react-calendar__month-view {
  box-sizing: border-box;
  padding: 10px 30px 10px;

  background: var(--bg-widget);
}

.react-calendar__month-view__weekdays {
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
  font-size: 0.75em;
}

.react-calendar__month-view__weekNumbers .react-calendar__tile {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 0.75em;
  font-weight: bold;
}

.react-calendar__month-view__weekdays__weekday {
  text-transform: lowercase;
  padding: 10px 0 8px 0;

  font-size: 16px;
  line-height: 136%;
  color: var(--txt_2);

  font-weight: normal;
}

.react-calendar__month-view__weekdays__weekday abbr {
  text-decoration: none;

  font-size: 14px;
}

.react-calendar__month-view__weekdays__weekday:nth-child(6),
.react-calendar__month-view__weekdays__weekday:nth-child(7) {
  color: var(--add-txt_1);
}


.react-calendar__tile {
  max-width: 100%;
  background: none;
  text-align: center;
  box-sizing: border-box;
  padding: 10px 0 8px 0;

  line-height: 136%;

  color: var(--txt_1);

  font-size: 16px;
}

.react-calendar--selectRange .react-calendar__tile--hover {
  color: var(--on_primary);
  background: var(--primary_hover);
}

.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background: var(--surface_01);
}

.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: var(--primary_active);
}

.react-calendar__month-view__days__day--neighboringMonth {
  color: var(--txt_3);
}

.react-calendar__tile:enabled:hover {
  color: var(--txt_1);
  background: var(--surface_01);
}


.react-calendar--selectRange .react-calendar__tile--hover {
  background: var(--primary_hover);
}

.react-calendar__tile--active,
.react-calendar__tile--hasActive {
  color: var(--on_primary);
  background: var(--primary_active);
}


.react-calendar__navigation__prev2-button {
  border-top-left-radius: 5px;
}

.react-calendar__navigation__next2-button {
  border-top-right-radius: 5px;
}

.react-calendar__navigation button[disabled] {
  color: var(--txt_3);
  background: transparent;
}

.react-calendar__year-view .react-calendar__tile,
.react-calendar__decade-view .react-calendar__tile,
.react-calendar__century-view .react-calendar__tile {
  box-sizing: border-box;
  padding: 10px;
}

.react-calendar__year-view__months,
.react-calendar__century-view,
.react-calendar__decade-view__years {
  padding: 32px 14px 33px;
  background: var(--bg-widget);
}


.react-calendar__tile:disabled {
  color: var(--txt_3);
  background-color: transparent;
  cursor: no-drop;
}

.calendar__data {
  margin: 0;
}
